import React from "react";

import mealBaner from '../../assets/baner.jpg';

export const Header = props =>{
    return(
        <>
            <header>
                <h1>ReactMeals</h1>
                <button>Cart</button>
            </header>
            <div>
                <img src={mealBaner} alt="a table full of organic food" />
            </div>
        </>
    );
}

export default Header;